	module.exports = {"data":{"markdownRemark":{"html":"<p>1976年に「鄧小平（Deng Xiaoping）」と続きます。幸いにも、人々は今日このような問題に興味を持っていません。この観点から、合理的な政治的生活秩序は、国の政治的生活にとっていかに重要であるか。この分野の資料は、プロットからはるかに発展しており、学術研究ではそれほど多くはありません。どのように中国の政治発展の浮き沈みのいくつかの教訓をまとめ、中国の政治発展を導くことは非常に重要です。</p>\n<p>日本研究センターで復旦大学の日本人学生を見た。彼は日本の現在の「中国脅威論」のパラドックスについて語ったが、それは支配的ではない。日本は核兵器開発を心配しているが、日本が朝鮮半島の核兵器開発を心配すれば、日本は核兵器を開発するだろうか。彼は、日本は3つの非核原則を遵守し、米国との安全保障条約はまだ実施されており、発展しないと述べた。これはアジア太平洋地域の国々の懸念事項であり、日本には技術的な問題はなく、国際的なパターンに変化があれば発展するだろうか？</p>","id":"/home/swain/workspace/political-life/src/pages/ja/0043.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ja/0043.md","frontmatter":{"title":"2月26日 土曜日"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ja/0043.md absPath of file >>> MarkdownRemark"}}

